@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;700&display=swap);
@font-face {
    font-family: PokemonFont;
    src: url(/frontend-react-pokemon/static/media/PokemonSolid.8adcdd6b.woff);
}
body {
    font-family: 'Inconsolata', monospace;
    margin: 0;
    padding: 0;
    overflow: auto;
}

button:hover {
    cursor: pointer;
}



.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color: #ffaa00;
    background-image: url(/frontend-react-pokemon/static/media/liquid-cheese.4567a8fb.svg);
    /*background-image: url("assets/blob-scene-haikei.svg");*/
    background-attachment: fixed;
    background-size: cover;
}

.main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: flex-end;
}
.all-container {
    width: 90vw;
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    justify-content: center;
}

.navigation-button {
    font-family: PokemonFont, serif;
    -webkit-text-stroke: 2px #4471ee;
    -webkit-text-fill-color: #fff90f;
    padding: 0 25px 15px 25px;
    font-size: 1.7em;
    border-radius: 5px;
    background-color: #def2fd;
    border: 5px solid #1f479b;
}

.navigation-buttons-wrapper * {
    margin-left: 20px;
}

.main-nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.main-nav li {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px;
    margin: 3px;
}

.main-nav a {
    color: #4471ee;
    font-weight: bold;
    font-size: 1.7em;
    text-decoration: none;
}

.main-nav li:hover {
    font-weight: bold;
    margin: 0;
    text-decoration: underline;
    border-radius: 5px;
    background-color: #def2fd;
    border: 3px solid #1f479b;
}

#home-pokeball, #item-pokeball {
    visibility: hidden;
}

#nav-home:hover ~ #home-pokeball {
    visibility: visible;
}

#nav-item:hover + #item-pokeball {
    visibility: visible;
}

.pokemon-card {
    min-width: 225px;
    width: 15%;
    margin: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 3px solid #1f479b;
    padding-bottom: 20px;
}

.pokemon-card h3 {
    font-size: 1.5em;
    margin-bottom: 0;
}

.pokemon-card img {
    width: 120%;
    margin: 0;
    padding: 0;
}

.pokemon-id {
    position: relative;
    left: 10px;
    top: 10px;
    font-weight: bold;
    align-self: flex-start;
    border-radius: 1rem;
    padding: 0.2rem 0.4rem;
    background-color: #def2fd;
}

.property-title {
    font-weight: bold;
    white-space: nowrap;
}

.pokemon-card * {
    margin: 3px;
}

.property-title p {
    display: inline;
    font-weight: normal;

}

.ability {
    text-align: center;
    width: 80%;
    margin: 0px 25px 3px 25px;
    padding: 5px;
    border-radius: 3px;
    background-color: rgba(194, 194, 194, 0.72);
    border: 1px solid #1f479b;

}

.rock {
    background-color: rgb(148, 81, 81);
}
.ghost {
    background-color: rgb(247, 247, 247);
}
.electric {
    background-color: rgb(255, 255, 161);
}
.bug {
    background-color: #F6D6A7;
}
.poison {
    background-color: #e0a7f6;
}
.normal {
    background-color: #F4F4F4;
}
.fairy {
    background-color: rgba(255, 192, 203, 0.863);
}
.fire {
    background-color: #FBE3DF;
}
.grass {
    background-color:#E2F9E1;
}
.water {
    background-color:#E0F1FD;
}
.item-card {
    min-width: 225px;
    width: 15%;
    margin: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 3px solid #1f479b;
    padding-bottom: 20px;
    background-color: rgba(208,243,148,0.72);
}

.item-card h3 {
    font-size: 1.5em;
    margin-bottom: 0;
}

