.item-category {
    position: relative;
    left: 10px;
    top: 10px;
    font-weight: bold;
    font-size: 0.9em;
    align-self: flex-start;
    border-radius: 1rem;
    padding: 0.2rem 0.4rem;
    background-color: #def2fd;
}

.property-title {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-weight: bold;
}

.item-card * {
    margin: 3px;
}

.property-title p {
    /*display: inline;*/
    padding: 0 15px 0 15px;
    font-weight: normal;
    word-break-inside: false;
    white-space: pre-wrap;
    word-break: break-word;
}

.property-title h4 {
    text-align: center;
}

.attribute {
    text-align: center;
    width: 80%;
    margin: 0px 25px 3px 25px;
    padding: 5px;
    border-radius: 3px;
    background-color: rgba(194, 194, 194, 0.72);
    border: 1px solid #1f479b;
}

.item-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 15px;
}

.item-cost {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    padding: 3px 12px 3px 0;
    width: 85%;
    background-color: #def2fd;
    border-radius: 5px;
    font-weight: bold;
    color: #1f479b;
    font-size: 1.4em;
}

.item-card img {
    width: 35px;
}

