.search-container {
    width: 495px;
    display: flex;
    position:relative;
    padding:0;
    margin-right:70px;
}

#search-bar {
    width: 100%;
    height: 2.5em;
    margin:0;
    padding: 0 20px;
    font-size: 1rem;
    border: 1px solid#def2fd;
    outline: none;
}

.search-icon {
    width: 70px;
    position: absolute;
    bottom:-12px;
    left:440px;
}

#search-bar:focus {
    border: 1px solid #1f479b;
    transition: 0.35s ease;
    color: #1f479b;
}

#search-bar:focus::-webkit-input-placeholder{
    -webkit-transition: opacity 0.45s ease;
    transition: opacity 0.45s ease;
    opacity: 0;
}

#search-bar:focus::-moz-placeholder {
    -moz-transition: opacity 0.45s ease;
    transition: opacity 0.45s ease;
    opacity: 0;
}

@media only screen and (max-width: 840px) {
    .search-container {
        width: 350px;
        position:relative;
        padding:0;
        margin-right:50px;
    }
    .search-icon {
        width: 45px;
        position: absolute;
        bottom:0;
        left:340px;
    }
}

@media only screen and (max-width: 680px) {
    .search-container {
        width: 250px;
        position:relative;
        padding:0;
        margin-right:50px;
    }
    .search-icon {
        width: 40px;
        position: absolute;
        bottom:0;
        left:215px;
    }
}
.pokemon-details img {
    position: absolute;
    height: 550px;
    right: -250px;
    top: -30px;
    margin: 0;
    padding: 0;
    z-index: 9;
}

.pokemon-details {
    min-width: 545px;
    width: 50%;
    margin: 20px;
    border-radius: 10px;
    display: flex;
    border: 3px solid #1f479b;
    padding-bottom: 20px;
    position: relative;
}

.details-wrapper {
    width: 75%;
}


.pokemon-details h3 {
    display: flex;
    justify-content: center;
    font-size: 3em;
    color: #1f479b;
}

.pokemon-id {
    display: inline-block;
    font-size: 1.2em;
    font-weight: bold;
    border-radius: 1rem;
    background-color: #def2fd;
}

.property-title {
    padding: 3px 0 3px 20px;
    font-weight: bold;
    white-space: nowrap;
    display: block;
    font-size: 1.4em;
    color: #1f479b;
}

.property-mini-title {
    /*padding: 3px 0 3px 20px;*/
    font-weight: bold;
    white-space: nowrap;
    display: block;
    font-size: 1.3em;
    color: rgba(0, 0, 0);
}

.pokemon-details * {
    margin: 3px;
}

.property-title p {
    font-size: 0.9em;
    color: black;
}

.ability {
    text-align: center;
    width: 40%;
    margin: 5px 25px 5px 25px;
    padding: 7px;
    border-radius: 3px;
    background-color: rgba(194, 194, 194, 0.72);
    border: 1px solid #1f479b;

}

@media only screen and (min-width: 1015px) {
    .pokemon-details img {
        height: 550px;
    }
}
@media only screen and (max-width: 1014px) {
    .pokemon-details img {
        height: 430px;
        right: -180px;
        top: -30px;
    }
}
@media only screen and (max-width: 820px) {
    .pokemon-details img {
        height: 400px;
        right: -100px;
        top: 40px;
    }

    .pokemon-details {
        margin: 10px;
        border-radius: 10px;
    }

    .pokemon-details h3 {
        font-size: 2em;
    }

    .pokemon-id {
        font-size: 1em;
    }

    .property-title {
        padding: 2px 0 2px 15px;
        font-size: 1.2em;
    }

    .property-mini-title {
        font-size: 1.1em;
    }

    .pokemon-details * {
        margin: 2px;
    }

    .property-title p {
        font-size: 0.7em;
    }
}

@media only screen and (max-width: 640px) {
    .pokemon-details img {
        height: 350px;
        right: -30px;
        top: 40px;
    }
}

.error-title {
    width: 50%;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.error-title h1 {
    position: absolute;
    height: 1em;
    font-family: PokemonFont, serif;
    top: -280px;
    -webkit-text-stroke: 2px #4471ee;
    -webkit-text-fill-color: #fff90f;
    font-size: 10em;
    -webkit-animation: zoom-out 0.5s ease-out;
            animation: zoom-out 0.5s ease-out;
}

.error-title p {
    position: absolute;
    display: block;
    top: 200px;
    color: #1f479b;
    font-size: 1.5em;
    font-weight: bold;
    -webkit-animation: invis-to-vis 0.8s ease-in;
            animation: invis-to-vis 0.8s ease-in;
}

@-webkit-keyframes zoom-out {
    0% {
        font-size: 50em;
        /*transform: scale(20, 20);*/
    }
    40% {
        font-size: 30em;
        /*transform: scale(10, 10);*/
    }
    70% {
        font-size: 10em;
        /*transform: scale(1, 1);*/
    }
    80% {
        font-size: 13em;
        /*transform: scale(3, 3);*/
    }
    100% {
        font-size: 10em;
        /*transform: scale(1, 1);*/
    }
}

@keyframes zoom-out {
    0% {
        font-size: 50em;
        /*transform: scale(20, 20);*/
    }
    40% {
        font-size: 30em;
        /*transform: scale(10, 10);*/
    }
    70% {
        font-size: 10em;
        /*transform: scale(1, 1);*/
    }
    80% {
        font-size: 13em;
        /*transform: scale(3, 3);*/
    }
    100% {
        font-size: 10em;
        /*transform: scale(1, 1);*/
    }
}

@-webkit-keyframes invis-to-vis {

    0% {
        opacity: 0;
    }

    70% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }

}

@keyframes invis-to-vis {

    0% {
        opacity: 0;
    }

    70% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }

}
